.img{
    height: 53px;
    width: 120px;
    border-radius: 22px;
}
.navbar{
    height: 62px;
    background-color: aliceblue;
    border: 2px solid rgb(151, 140, 140);
    /* background-color: blueviolet;  */
    display: flex;
    justify-content: center;

}
.a{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.b{
    height: 20px;
    width: 500px;
    margin-top: 17px;
    border-radius: 10px;
    margin-left: 52px;
    margin-right: 20px;
}
.nav-item{
    margin-left: 64px;
    outline: none;
    text-decoration: none;
    color: black;
    font-size: 17px; 
    cursor: pointer; 
    border: none;
}
.btn{
    height: 33px;
    width: 70px;
    margin-top: 10px;
    margin-left: 20px;
    align-items: center;
    text-align: center;
    text-decoration: none;
    border-radius: 10px;
    cursor: pointer;
    font-size: 18px;
    /* border-radius: 12px; */
    background-color: rgb(84, 84, 244);
    color: aliceblue;
}
li{
    list-style: none;
}